<template>
    <div class="background">
        <div class="content">
            <div v-for="category in natureList" :key="category.id" class="category-container">
                <div class="category-title">
                    {{ category.name }}
                </div>
                <div class="grid-container">
                    <div v-for="item in category.list" :key="item.id" :class="['grid-item', { 'playing': isPlaying(item.id) }]">
                        <div class="icon-wrapper" @mouseenter="showTooltip(item.id)" @mouseleave="hideTooltip"
                            @click="toggleAudio(item.id, getMp3Url(category.id, item.id))">

                            <img :src="getImageUrl(category.id, item.id)" alt="icon" class="icon" />
                            <div class="tooltip" v-if="hoveredId === item.id">{{ item.name }}</div>
                        </div>
                     
                        <!-- 添加音量控制滑动条 -->
                        <input class="volume-slider" v-if="isPlaying(item.id)" type="range" min="0" max="1" step="0.01"
                            v-model="volumeControls[item.id]" @input="setVolume(item.id)">
                        <audio :ref="'audio-' + item.id"></audio>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import voiceList from '@/json/voiceList.js';

export default {
    name: 'NatureSounds',
    data() {
        return {
            natureList: [],
            hoveredId: null,
            playingAudios: {}, // 用于存储播放状态
            volumeControls: {}, // 用于存储每个音频的音量控制
        };
    },
    created() {
        this.natureList = voiceList.data.list;
        this.natureList.forEach(category => {
            category.list.forEach(item => {
                this.volumeControls[item.id] = 1; // 初始化每个音频的音量为 1（最大）
            });
        });
    },
    methods: {
        getImageUrl(type, image) {
            // console.log('Type:', type);
            // console.log('Image:', image);
            return require(`@/assets/img/${type}/${image}.png`);
        },
        getMp3Url(type, id) {
            // console.log('Type:', type);
            // console.log('id:', id);
            return `https://moonsleep.fit/mp3/${type}/${id}.mp3`;
        },
        showTooltip(id) {
            this.hoveredId = id;
        },
        hideTooltip() {
            this.hoveredId = null;
        },

        async toggleAudio(id, url) {
            console.log('toggleAudio:', id, url);
            const audioPlayer = this.$refs['audio-' + id][0];

            if (!this.playingAudios[id] || audioPlayer.paused) {
                if (audioPlayer.src !== url) {
                    audioPlayer.src = url;
                }
                audioPlayer.loop = true; // 设置无限循环
                audioPlayer.volume = this.volumeControls[id]; // 设置初始音量

                try {
                    await audioPlayer.play();
                    this.playingAudios[id] = true; // 记录播放状态
                } catch (error) {
                    console.error('Error playing audio:', error);
                }
            } else {
                audioPlayer.pause();
                this.playingAudios[id] = false; // 更新播放状态
            }
        },

        setVolume(id) {
            const audioPlayer = this.$refs['audio-' + id][0];
            audioPlayer.volume = this.volumeControls[id];
        },

        isPlaying(id) {
            return this.playingAudios[id];
        },
    }
};
</script>

